import React from "react";
class Components extends React.Component {
  constructor(props) {
    super(props);
    console.log(props);
    this.state = {
      count: 0
    };
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick() {
    this.setState({
      count: this.state.count + 1
    });
  }
  render() {
    return (
      <div>
        {this.state.count}
        <button onClick={this.handleClick}>Clcik</button>
      </div>
    );
  }
}
export { Components };
